
<template>
  <div class="takePart">
		<div class="ruleExplain">
			<div class="reject" @click="reject">
				<img src="../assets/close.png" alt="">
			</div>
			<h2 class="white">参与活动</h2>
			<div class="content green">
				<div class="activityBrief">
					<span class="strong">官服玩家</span><br>
					<span>官服玩家可在游戏内节日界面分享助力H5至微信或大神，即可参加活动</span>
				</div>
				
				<div class="btn" @click="openGame">
					<img src="../assets/btn.png" alt="">
					<div>打开游戏</div>
				</div>
				<div class="activityTime">
					<span class="strong">渠道服玩家</span><br>
					<span>渠道服玩家请前往网易大神App，搜索“梦幻手游精灵”，绑定角色ID后接可参与活动</span>
				</div>
					<div class="btn" @click="openGodlike">
						<img src="../assets/btn.png" alt="">
						<div>去大神参与活动</div>
					</div>
			</div>
		</div>	
	</div>
</template>
<script>

export default {
	name: 'takePart',
	props: ['windowOpen'],
	methods: {
		openGame(){
			window.location.href = 'https://game.163.com/open/my/'
		},
		openGodlike(){
			window.location.href = 'https://app.16163.com/ds/ulinks/'
		},
		reject() {
			this.$emit('update:windowOpen', false);
		}
	}
}
</script>

<style lang='less' scoped>
	.takePart {
		border-top: 1px solid transparent;
		.white {
			color: white;
		}
		.green {
			color: #829a7a;
		}
		.strong {
			font-weight: 700
		}
		.ruleExplain {
			position: relative;
			.reject {
				font-size: 40px;
				position: absolute;
				left: 50%;
				top: 1.01rem;
				transform: translateX(-50%);
				padding: 10px 20px;
				border-radius: 30px
			}
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 80%;
			margin: 0 auto;
			margin-top: .23rem;
			background: url('../assets/background.png') no-repeat;
			border: 1px solid transparent;
			background-size: 100%;
			> h2 {
				margin-top: 40px;
				font-size: 40px
			}
			>.content {
				text-align: left;
				width: 80%;
				margin: 0.1rem auto;
				line-height: 50px;
				> .btn {
					text-align: center;
					margin: 30px auto;
					position: relative;
					img {
						display: inline-block;
						width: 56%;
					}
					> div {
						position: absolute;
						color: white;
						font-size: 40px;
						font-weight: 700;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -53%)
					}
				}
			}
		}
		
	}
</style>



