import Vue from 'vue'
import Router from 'vue-router'
import Config from '@/pages/config'
import HomePage from '@/pages/homePage'
import SucessPage from '@/pages/sucessPage'
import Congratulate from '@/pages/congratulate'
import Rule from '@/pages/rule'
import TakePart from '@/pages/takePart'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'homePage',
      component: HomePage
    },
    {
      path: '/sucessPage',
      name: 'sucessPage',
      component: SucessPage
    },
    {
      path: '/congratulate',
      name: 'congratulate',
      component: Congratulate
    },
    {
      path: '/rule',
      name: 'rule',
      component: Rule
    },
    {
      path: '/takePart',
      name: 'takePart',
      component: TakePart
    },
    {
      path: '/',
      name: 'congif',
      component: Config
    }
  ]
})



