<template>
  <div class="sucessPage">
		<div class="helpSucess green">
				<!-- <div class="reject" @click="reject">x</div> -->
				<h2 class="white">助力成功</h2>
				<div>诀窍提示：每天都可以给同一作品助力一次哦</div>
				<section>
					<div class="machine">
						<dl class="rotate_box clear">
							<dd class="one" id="one">
								<!-- <img src="../assets/scrollTop.png" alt=""> -->
							</dd>
							<dd class="two" id="two">
								<!-- <img src="../assets/icon2.png" alt=""> -->
							</dd>
							<dd class="three" id="three">
								<!-- <img src="../assets/icon3.png" alt=""> -->
							</dd>
						</dl>
						<!-- <a class="poiner" href="">
						<img src="../tigerMachine/images/poiner.png" alt="">
						</a>
						<a class="rotate_btn" href="javascript: void(0);">
						<img src="../tigerMachine/images/rocker.png" alt="">
						</a> -->
						<!-- <div class="btn_box">
							<img src="../tigerMachine/images/share_btn.png" alt="">
						</div> -->
					</div>
				</section>
				<div v-if="allFlag" class="resText">{{resText}}</div>
				<div v-if="allFlag" class="allpoints"> + {{allpoints}}</div>
				<div class="btn" @click="reject">
					<img src="../assets/btn.png" alt="">
					<div class="white">好 的</div>
			</div>
		</div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
	name: 'sucessPage',
	props: ['windowOpen', 'allpoints'],
	data() {
		return {
			msg: [100 , 200 , 300],
			point: 8,
			resText: '缘分上佳，积分大增！',
			oneFlag: false,
			twoFlag: false,
			threeFlag: false,
			allFlag: false,
		}
	},
	watch: {
		oneFlag(val) {
			this.allFlagChagne()
		},
		twoFlag() {
			this.allFlagChagne()
		},
		threeFlag() {
			this.allFlagChagne()
		}
	},
	mounted() {
		// this.init()
		let _this = this;
		console.log(_this.allpoints, 'allpointsallpointsallpoints')
		if(_this.allpoints) {
			setTimeout(function(){
				_this.init();
			},500)
		}
		
	},
	methods: {
		reject() {
			this.$emit('update:windowOpen', false);
			// this.ceshi()
		},
		allFlagChagne() {
			if(this.oneFlag && this.twoFlag && this.threeFlag) {
				this.allFlag = true
			}else {
				this.allFlag = false
			}
		},
		// init() {
		// 	let one = document.getElementById('one');
		// 	let two = document.getElementById('two');
		// 	let three = document.getElementById('three');
		// 	let _this = this;
		// 	let formData = new FormData();
		// 	formData.append("role_id","179163154");
		// 	let url = `/api/mhshow_h5/assistance`
		// 	axios.post(url,formData).then((res) => {
		// 		console.log(res, 'resssssssssssssssssssssssssss')
		// 		if(res && res.data) {
		// 			_this.point = res.data.points
		// 			if(_this.points === 10) {
		// 				one.style.backgroundPositionY = '-2072px'
		// 				two.style.backgroundPositionY = '-2072px'
		// 				three.style.backgroundPositionY = '-2072px';
		// 				_this.resText = '缘分深厚，积分狂增！'
		// 			}else if(_this.points === 8) {
		// 				one.style.backgroundPositionY = '-2072px'
		// 				two.style.backgroundPositionY = '-1820px'
		// 				three.style.backgroundPositionY = '-1560px'
		// 				_this.resText = '缘分上佳，积分大增！'
		// 			}else if(_this.points === 5) {
		// 				one.style.backgroundPositionY = '-2072px'
		// 				two.style.backgroundPositionY = '-1820px'
		// 				three.style.backgroundPositionY = '-1560px'
		// 				_this.resText = '运气不错，积分增加！'
		// 			}
		// 		}
		// 	}).catch(err => {
		// 		console.log(err.response,'responseresponseresponseresponse' );
		// 		alert('今天已点赞啦，客官明日再来吧')
		// 	})
		// },
		init() {
			console.log(this.point, 'allpointsallpointsallpoints');
			let _this = this;
			let one = document.getElementById('one');
			let two = document.getElementById('two');
			let three = document.getElementById('three');
			if(this.allpoints === 10) {
				let random = parseInt(Math.random() * 3);
				console.log(random, 'randomrandomrandomrandom')
				let positionY = '';
				if(random === 0) {
					positionY = '-2072px'
				} else if(random === 1) {
					positionY = '-1820px'
				} else if(random === 2) {
					positionY = '-1560px'
				}
				one.style.backgroundPositionY = positionY
				two.style.backgroundPositionY = positionY
				three.style.backgroundPositionY = positionY
				this.resText = '缘分深厚，积分狂增！'
			}else if(this.allpoints === 8) {
				let randomX = parseInt(Math.random() * 3);
				let randomY = parseInt(Math.random() * 3);
				let randomFlag = parseInt(Math.random() * 3);
				let allArr = [];
				let arr = [];
				while(randomX === randomY) {
					randomX = parseInt(Math.random() * 3)
					randomY = parseInt(Math.random() * 3)	
				}
				if(randomFlag === 0) {
					arr = [randomX, randomX, randomY]
				} else if(randomFlag === 1){
					arr = [randomX, randomY, randomX]
				} else if(randomFlag === 2) {
					arr = [randomX, randomY, randomY]
				}
				for(let i = 0; i < arr.length; i++) {
					let heightPx = ''
					if(arr[i] === 0) {
						heightPx = '-2072px'
					} else if(arr[i] === 1) {
						heightPx = '-1820px'
					} else if(arr[i] === 2) {
						heightPx = '-1560px'
					}
					allArr.push(heightPx)
				}
				one.style.backgroundPositionY = allArr[0]
				two.style.backgroundPositionY = allArr[1] 
				three.style.backgroundPositionY = allArr[2]
				this.resText = '缘分上佳，积分大增！'
			}else if(this.allpoints === 5) {
				let randomX = parseInt(Math.random() * 3);
				let randomY = parseInt(Math.random() * 3);
				let randomZ = parseInt(Math.random() * 3);
				let allArr = [];
				while((randomX === randomY) || (randomX === randomZ) || (randomY === randomZ)) {
					 randomX = parseInt(Math.random() * 3);
					 randomY = parseInt(Math.random() * 3);
					 randomZ = parseInt(Math.random() * 3);
				}
				let arr = [randomX, randomY, randomZ];
				for(let i = 0; i < arr.length; i++) {
					let heightPx = '';
					if(arr[i] === 0) {
						heightPx = '-2072px'
					} else if(arr[i] === 1) {
						heightPx = '-1820px'
					} else if(arr[i] === 2) {
						heightPx = '-1560px'
					}
					allArr.push(heightPx)
				}
				one.style.backgroundPositionY = allArr[0]
				two.style.backgroundPositionY = allArr[1]
				three.style.backgroundPositionY = allArr[2]
				this.resText = '运气不错，积分增加！'
			}
			one.addEventListener('transitionend', function() {
				_this.oneFlag = true	
			})
			two.addEventListener('transitionend', function() {
				_this.twoFlag = true
			})
			three.addEventListener('transitionend', function() {
				_this.threeFlag = true
			})
		}
	}
}
</script>

<style lang='less'>
	* {
		margin: 0;
		padding: 0
	}
	.green {
		color: green;
	}
	.white {
		color: white;
	}
	.sucessPage {
		border-top: 1px solid transparent;
		.helpSucess {
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			width: 80%;
			margin: 0 auto;
			margin-top: .17rem;
			background: url('../assets/background.png') no-repeat;
			border: 1px solid transparent;
			background-size: 100%;
			> h2 {
				margin-top: 40px;
				font-size: 40px
			}
			> div {
				margin-top: 40px
			}
			> div.resText{
				margin-top: 0
			}
			> div.allpoints{
				margin-top: 10px;
				font-size: 40px;
				font-style:italic;
				font-weight: 700
			}
			> section {
				width: 62%;
				margin: 40px auto;
				.machine {
					position: relative;
					height: 400px;
					background: url('../assets/tiger.png') no-repeat;
					border-top: 1px solid transparent;
					.rotate_box {
						position: relative;
						margin-top: 76px;
						margin-left: 10px;
						> .one {
							// width: 20%;
							// height: 200px;
							// background: url('../assets/scrollTop.png') no-repeat 0px -82px
						}
						> dd {
							float: left;
							width: 21%;
							height: .14rem;
							text-align: center;
							margin-left: 33px;
							background: url('../assets/newScroll.png') no-repeat 0px -82px;
							// transition: all 1s;
						}
						> dd:nth-child(1) {
								transition: all 1s;
						}
						> dd:nth-child(2) {
								transition: all 2s;
						}
						> dd:nth-child(3) {
								transition: all 3s;
						}
					}
				}
			}
			> .btn {
				margin: 30px auto;
				position: relative;
				img {
					display: inline-block;
					width: 50%;
				}
				> div {
					position: absolute;
					color: white;
					font-size: 40px;
					font-weight: 700;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -53%)
				}
			}
		}
	}
</style>



