		// 		console.log(val, 'falsefalsefalsefalsefalsefalsefalsefalse');
		// 		this.CloseDiv(mo)
		// 	}
		// }
	},
  data () {
    return {
		 	// 初始化分享组件
			mobileShare : new MobileShare({
