	methods: {
		ShowDiv(mo) {
			document.body.style.overflow='hidden';
			document.addEventListener("touchmove",mo,false);//禁止页面滑动
		},
		CloseDiv(mo) {
			document.body.style.overflow='';//出现滚动条
    	document.removeEventListener("touchmove",mo,false);
		},
		init() {
			let _this = this;
			let token = localStorage.getItem('wxcode') || 'da778501bdd44a229a6b1fba4f80899a'
			let role_id = localStorage.getItem('role_id') || '179163154'
			let url = `/api/mhshow_h5/get_roleinfo?role_id=${role_id}&token=da778501bdd44a229a6b1fba4f80899a&token_type=wx`;
			axios.get(url).then((res) => {
				if(res && res.data && res.data.result) {
					_this.roleData = res.data.result
					_this.role_name = _this.roleData.role_name ? _this.roleData.role_name : '';
					_this.user_image = _this.roleData.user_image ? _this.roleData.user_image : head;
					_this.points = _this.roleData.points ? _this.roleData.points : '';
					_this.show_img = _this.roleData.show_img ? _this.roleData.show_img : person;
				}
			}).catch(error => {
				console.log(error, 'errorerrorerrorerror')
			})
		},
		getFriendList() {
			let _this = this;
			let url = `/api/mhshow_h5/get_role_assistances?role_id=${this.role_id}&user_num=5`;
			axios.get(url).then((res) => {
				if(res && res.data && res.data.result) {
					let result = res.data.result
					for(let i = 0; i < result.length; i++) {
						if(result[i].points = 10)	{
							result[i].message = '缘分深厚，积分狂增!'	
						}else if(result[i].points = 8) {
							result[i].message = '缘分上佳，积分大增!'	
						}else {
							result[i].message = '运气不错，积分增加!'	
						}
					}
					_this.friendArr = result;
				}
				if(res && res.data && res.data.broadcast) {
					let broadcast = res.data.broadcast
					for(let i = 0; i < broadcast.length; i++) {
						if(broadcast[i] === 50) {
							broadcast[i].src = pic50	
						}	else if(broadcast[i] === 100) {
							broadcast[i].src = pic100	
						} else if(broadcast[i] === 200) {
							broadcast[i].src = pic200
						} else {
							broadcast[i].src = pic300
						}
					}
					_this.broadcast = res.data.broadcast;
				}
			})	
		},
		helpDay() {
			let _this = this;
			let formData = new FormData();
			formData.append("role_id","179163154");
			let url = `/api/mhshow_h5/assistance`
			axios.post(url,formData).then((res) => {
				if(res && res.data) {
					_this.points = res.data.points;
					_this.windowOpen = true;
					_this.windowName = 'sucessPage'
				}
