<template>
  <div class="verify">
    正在验证3...
     <div class="layout" v-if="login">
      <div class="login">
        <div>确认角色信息</div>
        <div>游戏昵称昵称</div>
        <div class="foot">
          <button @click="changeNext" id="trigger5">换一个</button>
          <button>参与活动</button>
        </div>
      </div>
     </div>
  </div>
</template>

<script>
import axios from 'axios'
import swal from 'sweetalert';
import MobileSelect from 'mobile-select'
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      login: false,
      selectList: []
    }
  },
  methods: {
    changeNext() {
      this.login = false;
    },
    getParameter: function (name) {
      var query = window.location.search;
      var iLen = name.length;
      var iStart = query.indexOf(name);
      if (iStart == -1)
          return "";
      iStart += iLen + 1;
      var iEnd = query.indexOf("&", iStart);
      if (iEnd == -1)
          return query.substring(iStart);
      return query.substring(iStart, iEnd);
    },
    isWeiXin: function() {
      var ua = window.navigator.userAgent.toLowerCase();
      if (ua.match(/MicroMessenger/i) == 'micromessenger') {
          return true; // 是微信端
      } else {
          return false;
      }
    },
    mobileSelect() {
      let _this = this;
      let mobileSelect = new MobileSelect({
        trigger: "#trigger5",
        wheels: [
          {
            data: _this.selectList
          }
          // {
          //   data:[
          //     {
          //       id:1,
          //       value: '123213213213',
          //       name: 'cehsi1'
          //     },
          //     {
          //       id:2,
          //       value: 'gfdgfdhfghf',
          //       name: 'cehsi2'
          //     }
          //   ]
          // }
        ],
        callback:function(indexArr, data){
          console.log(data);
        }
      });
    }
  },
  mounted () {
    let _this = this;
    // this.mobileSelect()
    // swal({
    //   title: "确认角色信息",
    //   text: "游戏昵称昵称",
    //   buttons: {
    //     button1: {
    //       text: '换一个',
    //       value: true,
    //       id: 'change'
    //     },
    //     button2: {
    //       text : "参与活动",
    //        value: false,
    //     }
    //   },
    //   // icon: "success",
    // }).then((value) => {
    //   // _this.dsEnter();
    //   if(value) {
    //     document.getElementById('trigger5').click()
    //     console.log('换一个换一个');
    //   } else {
    //     console.log('参与活动')
    //   }
    // })
    var role_id = this.getParameter('role_id') || '179163154';
    //微信端验证
    if (this.isWeiXin()) {
      
      let url = 'https://exth5.dev.16163.com?role_id='
      let callbackUrl = 'https%3a%2f%2fexth5.dev.16163.com%3frole_id%3d';
      let redirect_uri = 'https%3a%2f%2fgame.16163.com%2fmhshow_h5%2fxxxx%3frole_id%3d1234';
      let code = this.getParameter('code');
      let appid = 'wx0376800bd1071c94';

      //微信回调含code调用免登陆接口，否则跳转微信验证获取code
      if (code) {
          document.write('已获取code...')
          localStorage.setItem('wxcode',code)
          localStorage.setItem('role_id',role_id)
          this.$route.push('/index')
          
      } else {
        document.write('跳转至微信验证...')
        window.location.href = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${appid}&redirect_uri=${callbackUrl}${role_id}&response_type=code&scope=snsapi_base&state=mhshow_h5`;
      }
    } else {
      //大神端验证
      ds.ready().then(function() {
        if (ds.isGodlike) {
          alert('已进入大神客户端')
          // ds.checkLogined().then(function(res) {
          //   alert('checkLoginedcheckLoginedcheckLogined')
          //   alert(JSON.stringify(res));
          //   if(res && res.isLogined) {
          //     ds.getAccessToken({
          //       appId: '5d81e28d1da86cffea4b7829'
          //     }).then(function(res) {
          //       alert(JSON.stringify(res));
          //       if(res && res.code === '200') {
          //         ds.getBindGameRoleList({
          //           gameCode: '',
          //           minLevel: 0
          //         }).then(function(res) {
          //           alert(JSON.stringify(res));
          //           if(res && res.code === '200') {
          //             alert(res)
          //           }
          //         });
          //       }
          //     });
          //   }
          // });
          ds.checkLogined().then((res) => {
            alert('checkLogined\n' + JSON.stringify(res))
            return res
          }).then(res => {
            if(res && res.result.isLogined) {
              // ds.getAccessToken({
              //   appId: '5d81e28d1da86cffea4b7829'
              // }).then((res) => {
              //   alert('getAccessToken\n' + JSON.stringify(res));
              //   return res
              // })
              ds.getBindGameRoleList({
                 gameCode: '',
                 minLevel: 0
               }).then((res) => {
                 alert('getBindGameRoleList\n' + JSON.stringify(res));
                 if(res && res.result && res.result.list.length > 0) {
                  alert(JSON.stringify(res.result));
                  _this.login = true;
                  let arr = [];
                  for(let i = 0; i < res.result.length; i++) {
                    let obj = {};
                    obj.id = i;
                    obj.roleId = res.result[i].roleId;
                    obj.icon = res.result[i].icon;
                    obj.serverName = res.result[i].serverName;
                    obj.nick = res.result[i].nick;
                    obj.value = `${res.result[i].nick} - ${res.result[i].serverName}`
                    arr.push(obj);
                  }

                  _this.selectList = arr
                  _this.mobileSelect()
                 } else {
                    swal({
                      // title: "Good job!",
                      text: "登录且绑定梦幻手游角色后即可参与活动！",
                      button: "去登陆",
                      // icon: "success",
                    }).then(() => {
                      ds.openGameRoleBindingPage({
                        gameCode: 'g78'
                      });
                    })
                 }
               })    
            }else if (res && !res.result.isLogined) {
              swal({
                // title: "Good job!",
                text: "绑定梦幻手游角色后即可参与活动！",
                button: "去绑定",
                // icon: "success",
              }).then(() => {
                ds.openLoginPage();
              })
            }
          })
          //外部浏览器验证
        } else {
          alert('已进入外部浏览器')
          this.$router.push('/index')
          // var mobileShare = new MobileShare(options); // 初始化分享组件
          // mobileShare.show(); 
          // console.log(mobileShare)
        }
      });
    }



  },
}
</script>


<style lang='less' scoped>
  .swal-footer {
    text-align: center
  }
  .verify {
    .swal-button {
      background-color: #7cd1f9;
      color: #fff;
      border: none;
      box-shadow: none;
      border-radius: 5px;
      font-weight: 600;
      font-size: 14px;
      padding: 10px 24px;
      margin: 0;
      cursor: pointer;
    }
    .layout {
      position: fixed;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			background: #000000;
			height: 100%;
			background:rgba(0, 0, 0, 0.5);
      z-index: 999;
      // font-size:  32;
      > .login {
        border-radius: 5px;
        position: absolute;
        width: 478px;
        height: .3rem;
        background-color: #ffffff;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        > div:nth-child(1) {
          margin-top: 40px;
        }
        > div:nth-child(2) {
          margin-top: 40px;
          color: green;
        }
        > .foot {
          text-align: center;
          margin-top: 40px;
        }
        button {
          background-color: #7cd1f9;
          color: #fff;
          border: none;
          box-shadow: none;
          border-radius: 5px;
          font-weight: 600;
          font-size: 14px;
          padding: 10px 24px;
          margin: 0;
          cursor: pointer;
        }
      }
    }
  }
</style>



