
<template>
  <div class="congratulate">
		<div class="bg green">
			<div class="reject" @click="reject">
				<img src="../assets/close.png" alt="">
			</div>
			<h2 class="white">恭喜获奖</h2>
			<div class="prizeName">奖品名称</div>
			<div class="box">
				<img :src="imgSrc" alt="">
				<div class="achieve" v-if="isAchieve">
					<img src="../assets/achieve.png" alt="">
				</div>
			</div>
			<div v-if="isAchieve === false">获得{{congratulateParise.score}}积分，即可获得此游戏物品！</div>
			<div v-if="isAchieve">梦幻秀作者已达成活动积分要求</div>
			<div v-if="isAchieve">登录游戏即可领取此奖品！</div>
			<div class="btn">
				<img src="../assets/btn.png" alt="">
				<div>立即参与</div>
			</div>
		</div>
	</div>
</template>
<script>
import pic50 from '../assets/integral1.png'
import pic100 from '../assets/integral2.png'
import pic200 from '../assets/integral3.png'
import pic300 from '../assets/integral4.png'
export default {
	name: 'App',
	props: ['windowOpen', 'congratulateParise', 'points'],
	data() {
		return {
			isAchieve: false,
			boxClass: 'box',
			imgSrc: ''
		}
	},
	mounted() {
		console.log(this.congratulateParise, this.points, 'congratulateParisecongratulateParisecongratulateParise')
		if(this.points && this.congratulateParise && this.congratulateParise.score) {
			if(this.points >= this.congratulateParise.score) {
				this.isAchieve = true
			}else {
				this.isAchieve = false
			}
			if(this.congratulateParise.score >= 300) {
				this.imgSrc = pic300
			} else if(this.congratulateParise.score >= 200) {
				this.imgSrc = pic200
			} else if(this.congratulateParise.score >= 100) {
				this.imgSrc = pic100
			} else if(this.congratulateParise.score >= 50) {
				this.imgSrc = pic50
			}
		}
	},
	methods: {
		reject() {
			this.$emit('update:windowOpen', false);
		}
	}
}
</script>

<style lang='less' scoped>
	.congratulate {
		position: relative;
		border-top: 1px solid transparent;
		.bgPic1 {
			background: url('../assets/integral1.png') no-repeat;
			background-size: 100%
		}
		.bgPic2 {
			background: url('../assets/integral2.png') no-repeat;
			background-size: 100%
		}
		.bgPic3 {
			background: url('../assets/integral3.png') no-repeat;
			background-size: 100%
		}
		.bgPic4 {
			background: url('../assets/integral4.png') no-repeat;
			background-size: 100%
		}
		.white {
			color: white;
		}
		.green {
			color: #829a7a;
		}
		> .bg{
			position: fixed;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			margin: 0 auto;
			margin-top: .17rem;
			width: 80%;
			background: url('../assets/background.png') no-repeat;
			background-size: 100%;
			border: 1px solid transparent;
			> .reject {
				font-size: 40px;
				position: absolute;
				left: 50%;
				top: 1.01rem;
				transform: translateX(-50%);
				padding: 10px 20px;
				border-radius: 30px;
				z-index: 99
			}
			> h2 {
				margin-top: 40px;
				font-size: 40px
			}
			> .prizeName {
				margin-top: 40px
			}
			> .box {
				height: .35rem;
				margin: 40px auto;
				width: 50%;
				background-color: #e4d8bc;
				border-radius: 10px;
				position: relative;
				> img {
					width: 90%
				}
				> .achieve {
					width: 20%;
					position: absolute;
					right: 0;
					top: 0;
					> img {
						width: 100%
					}
				}
			}
			> .btn {
				margin: 30px auto;
				position: relative;
				> 
				img {
					display: inline-block;
					width: 50%;
				}
				> div {
					position: absolute;
					color: white;
					font-size: 40px;
					font-weight: 700;
					left: 50%;
					top: 50%;
					transform: translate(-50%, -55%)
				}
			}
		}
	}
</style>



