
<template>
  <div class="rule">
		<div class="ruleExplain">
			<div class="reject" @click="reject">
				<img src="../assets/close.png" alt="">
			</div>
			<h2 class="white title">规则说明</h2>
			<div class="content green">
				<div class="activityBrief">
					<span class="strong title">活动简介：</span>
					<span>助力赢积分，北溟鲲抱回家！只要参与游戏梦幻秀助力H5活动，即能获得积分，赢取北冥鲲等大奖哦！</span>
				</div>
				<div class="activityTime marginTop">
					<span class="strong title">活动时间：</span>
					<span>2019年9月29日-2019年10月7月</span>
				</div>
				<div class="activityRule marginTop">
					<span class="strong title">活动规则：</span><br>
					<span>1丶玩家将游戏内的助力H5分享至微信或大神，即可在微信丶大神参与H5活动，获取积分，根据积分发放奖励。</span><br>
					<span>2丶所有奖励均通过游戏内邮件发放，玩家请及时登录游戏领取；玩家使用北冥鲲转盘道具奖</span>
				</div>
			</div>
		</div>	
	</div>
</template>
<script>

export default {
	name: 'rule',
	props: ['windowOpen'],
	methods: {
		reject() {
			this.$emit('update:windowOpen', false);
		}
	}
}
</script>

<style lang='less' scoped>
	.rule {
		position: relative;
		.title {
			font-size: 40px
		}
		.reject {
				font-size: 40px;
				position: absolute;
				left: 50%;
				top: 1.01rem;
				padding: 10px 20px;
				border-radius: 30px;
				z-index: 88;
				transform: translateX(-50%)
			}
		.white {
			color: white;
		}
		.green {
			color: #829a7a;
		}
		.marginTop{
			margin-top: 40px;
		}
		.strong {
			font-weight: 700;
		}
		.ruleExplain {
			position: fixed;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			width: 80%;
			margin: 0 auto;
			margin-top: .23rem;
			background: url('../assets/background.png') no-repeat;
			border: 1px solid transparent;
			background-size: 100%;
			> h2 {
				margin-top: 40px;
				font-size: 40px
			}
			>.content {
				text-align: left;
				width: 80%;
				margin: .1rem auto;
				.activityBrief {
					line-height: 50px
				}
				.activityRule {
					line-height: 50px
				}
			}
		}
		
	}
</style>



